<script>
import { register } from '@/api/user'
import { Notify } from 'vant'
export default {
  name: 'user_login',
  data () {
    return {
      username: '',
      password: '',
      avatar: '',
      bio: '',
      email: ''
    }
  },
  methods: {
    onSubmit (values) {
      console.log('submit', values)
    },
    onclickLeft () {
      console.log('left')
    },
    async onClickRegirster () {
      if (this.username === '' || this.password === '') {
        return
      }
      const res = await register(this.username, this.password, this.email, this.avatar, this.bio)
      if (res.code === 200) {
        // 顶部提示
        Notify({
          type: 'success',
          message: '注册成功'
        })
        this.$router.push('/login')
      }
    }
  }
}
</script>
<template>
    <div>
        <van-nav-bar
        title="用户注册"
        left-text="返回"
        left-arrow
        @click-left="$router.go(-1)"
        />
        <van-form @submit="onSubmit">
            <van-field
                v-model="username"
                name="用户名"
                label="用户名"
                placeholder="用户名"
                required
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                required
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <van-field
                v-model="email"
                type="email"
                name="邮箱"
                label="邮箱"
                placeholder="邮箱"
                :rules="[{ required: false, message: '请填写邮箱' }]"
            />
            <van-field
                v-model="avatar"
                type="url"
                name="头像"
                label="头像"
                placeholder="头像url"
                :rules="[{ required: false, message: '请填写头像' }]"
            />
            <van-field
            v-model="bio"
            rows="3"
            autosize
            label="简介"
            type="textarea"
            maxlength="80"
            placeholder="请输入简介"
            show-word-limit
            />
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit" @click="onClickRegirster">注册</van-button>
            </div>
            </van-form>
    </div>
</template>
<style>
.van-form{
    margin-top: 30px;
}
</style>
